@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Vollkorn:ital,wght@1,700&display=swap");

*,
*::after,
*::before {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
}

body {
    margin: 0;
    font-size: 1.8rem;
    color: #2b3e51;
    background-color: #eee;
    font-family: "Roboto Slab", serif;
    height: 100%;
    width: 100%;
}

.header {
    margin-top: 0px;
    height: 14vh;
    background: #343642;
    padding: 25px;
}

.headercontent {
    margin: 0%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 20px;
    height: 100%;
}

.headercontent p {
    font-weight: bolder;
}

.profile {
    font-size: 50px;
    color: #25d0ff;
    text-shadow: 0 0 20px #25d0ff;
    background-color: #2b2d37;
    border-radius: 50%;
    /* height: 80px;
    width: 80px; */
    display: flex;
    padding: 10px;
    justify-content: center;
    align-items: center;
    border: 3px solid #5f6379;
    transition: all 0.2s ease-in-out;
    margin-left: 20px;
}
.profile:hover {
    box-shadow: rgba(255, 255, 255, 0.3) 0 0 30px;
    cursor: pointer;
    /* font-size: 55px; */
    transform: scale(1.05);
}

nav {
    z-index: 3;
}

nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    position: relative;
    display: flex;
    justify-content: space-around;
}

nav ul li {
    display: inline-block;
    cursor: pointer;
}

nav ul ul {
    display: none;
    position: absolute;
    top: 85px;
    right: 90px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.8);
    margin-left: 10px;
    z-index: 999999;
}

nav ul li:hover > ul {
    display: inherit;
}

nav ul ul li {
    width: 280px;
    float: none;
    display: list-item;
    position: relative;
    z-index: 999999;
    background-color: #2b2d37;
    color: #25d0ff;
    padding: 10px;
    border: 1px solid #5f6379;
    font-weight: 800;
}

nav ul ul li span {
    color: white;
    font-weight: 300;
}

a {
    color: #25d0ff;
    text-decoration: none;
    font-size: 50px;
    font-family: "Roboto Slab", serif;
}

#allTheThings {
    width: 90%;
    max-width: 100%;
    text-align: center;
    margin: 12% auto;
    font-size: 1.3rem;
    color: #94aab0;
    display: block;
}

.homelogo {
    font-size: 150px;
}

#member {
    display: inline-block;
    padding: 20px 100px;
}

#verticalLine {
    display: inline-block;
    border-left: solid #343642;
}

#librarian {
    padding: 20px 100px;
}

#member,
#librarian {
    transition: all 0.2s ease-in-out;
}

#member:hover,
#librarian:hover {
    transform: scale(1.1);
}

.loginform {
    font-family: "Roboto Slab", serif;
    display: flex;
    flex-direction: column;
    width: 600px;
    align-items: center;
    /* justify-content: center; */
    margin: 100px auto;
    padding: 50px;
    border-radius: 20px;
    border: #555 solid 2px;
    background-color: #f5f5f5;
    box-shadow: rgba(0, 0, 0, 0.4) 0 0 20px;
}

.loginhead {
    color: #25d0ff;
    font-size: 40pt;
    font-weight: bold;
}

input {
    border: 1px solid #777;
    border-radius: 10px;
    padding: 10px;
    font-size: 20pt;
    margin: 30px auto;
}

input[type="submit"] {
    padding: 10px 50px;
    background-color: #25d0ff;
    color: white;
    font-weight: bold;
    font-size: 20pt;
    width: 300px;
    cursor: pointer;
}

input[type="submit"]:hover {
    background-color: white;
    color: #25d0ff;
    box-shadow: #25d0ff 0 0 20px;
}

.librarianhome {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 22px auto;
}

.libHomeButton {
    padding: 20px 50px;
    background-color: #25d0ff;
    color: white;
    font-weight: bold;
    font-size: 20pt;
    width: 500px;
    cursor: pointer;
    text-align: center;
    border: 1px solid #777;
    border-radius: 10px;
    margin: 30px auto;
}

.libHomeButton:hover {
    background-color: white;
    color: #25d0ff;
    box-shadow: #25d0ff 0 0 20px;
}

.studentHome {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 5% auto;
}

.studentButtons {
    display: flex;
    width: 100%;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
    margin-top: 150px;
}

.studHomeButton {
    padding: 10px 50px;
    background-color: #25d0ff;
    color: white;
    font-weight: bold;
    font-size: 20pt;
    width: 300px;
    cursor: pointer;
    text-align: center;
    border: 1px solid #777;
    border-radius: 10px;
    margin: 30px auto;
}

.studHomeButton:hover {
    background-color: white;
    color: #25d0ff;
    box-shadow: #25d0ff 0 0 20px;
}

.scannedbook {
    font-size: 20pt;
}
.qrbuttons {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.qrscanbutton {
    padding: 10px 20px;
    background-color: #25d0ff;
    color: white;
    font-weight: bold;
    font-size: 20pt;
    width: 200px;
    cursor: pointer;
    text-align: center;
    border: 1px solid #777;
    border-radius: 10px;
    margin: 0;
}
.qrscanbutton:hover {
    background-color: white;
    color: #25d0ff;
    box-shadow: #25d0ff 0 0 20px;
}

.searchButton {
    padding: 15px 50px;
    background-color: #25d0ff;
    color: white;
    font-weight: bold;
    font-size: 21pt;
    width: 350px;
    cursor: pointer;
    text-align: center;
    border: 1px solid #777;
    border-radius: 10px;
    margin: 30px auto;
}

.searchButton:hover {
    background-color: white;
    color: #25d0ff;
    box-shadow: #25d0ff 0 0 20px;
}

.faqsection {
    margin: 100px;
}

.ErrorPage {
    font-weight: bolder;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 18vh;
    width: 100%;
    /* height: 700px; */
    height: 86vh;

    /* -webkit-text-stroke: 2px black; */
    background-image: url("./img/404.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: #cccccc;
    font-family: "Vollkorn", serif;
    text-shadow: rgba(0, 0, 0, 0.3) 0 0 20px;
