.backtext {
    margin-left: 20px;
}

table {
    font-size: 15pt;
    margin-top: 40px;
}
th {
    font-size: 16pt;
}
td {
    padding: 5px 30px;
}
.cellcenter {
    text-align: center;
}

.scanner {
    margin-top: 50px;
    height: auto;
    width: 100%;
}

.books {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* margin: 5% auto; */
    width: 90%;
}

.book {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
