    width: 100%;
    border-radius: 10px;
    border: rgba(0, 0, 0, 0.3) 1px solid;
    cursor: pointer;
    font-size: 15pt;
    /* line-height: 1.5; */
    padding: 20px;
    padding-left: 30px;
    background-color: #555;
    color: white;
}

.book:hover {
    box-shadow: rgba(0, 0, 0, 0.8) 0 0 20px;
}

.bookname {
    font-size: 18pt;
    font-weight: bolder;
    width: 100%;
    text-align: center;
}

.bookresulthead {
    font-size: 20pt;
    font-weight: bolder;
    width: 80%;
    text-align: left;
    margin-top: 50px;
    color: #25d0ff;
    display: flex;
    align-items: center;
}

.book b {
    color: #25d0ff;
}

.faqul {
    margin-top: 0px;
}

.askquestion {
    /* margin-top: 50px; */
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    justify-content: center;
    /* width: 60%; */
    padding: 0 100px 0 100px;
}

.askhead {
    text-align: left;
    font-size: 25pt;
    font-weight: bold;
    color: #25d0ff;
}

.askques {
    width: 80%;
    margin: 0;
    height: 60px;
    font-size: 18pt;
    padding: 10px;
}
.askform {
