}

textarea {
    border: 1px solid #777;
    border-radius: 10px;
    padding: 10px;
    font-size: 17pt;
    font-weight: normal;
    margin: 30px auto;
    resize: none;
    width: 450px;
    height: 200px;
}

.noquestion {
    font-size: 25pt;
    font-weight: bold;
    color: #25d0ff;
    margin-top: 50px;
    width: 100%;
    text-align: center;
}

.searchimage {
    width: 100%;
    height: auto;
    margin-top: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    padding: 100px;
}

.pehlabaxa {
    margin: 0px;
}

.searchimgbuttons {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.dusrabaxa {
    margin: 0px;
    width: 800px;
