    font-size: 20pt;
    font-weight: bold;
    /* margin: 50px 0; */
    padding: 40px;
    width: 100%;
    border-radius: 0 0 20px 20px;
}

.loading {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: 30pt;
    height: 600px;
    width: 600px;
    font-weight: bold;
}

.searchimgbutton {
    display: flex;
    justify-content: center;
    align-items: center;
}

.nobookissued {
    font-size: 30pt;
    font-weight: bold;
}

.rbooks {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* margin: 5% auto; */
